@import "../../assets/styles/imports";
.homePage {
  &__content {
    display: grid;
    grid-template-columns: 270px 1fr;
    grid-gap: 30px;
    margin-top: 64px;
    .left {
      .button {
        margin-top: 50px;
      }
    }
    .right {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }
}
